body {
  background-color: black;
}

.gifs {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.gif {
  padding: 0 2px;
}

.loader {
  color: blue;
}

h5 {
    font-size: 12px;
}

.card {
  cursor: pointer;
}

.card-img-top {
    width: 100%;
    height: 15vw;
    object-fit: cover;
}

.card {
    margin: 10px;
    position: relative;
}

.modal-body a {
  word-break: break-all;
}

form {
  display: flex;
  align-content: space-around;
  justify-content: space-around;
  padding: .5em;
}

form input {
  min-width: 300px;
}
